body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{
    margin: 0;
    padding: 0;
}
.App{
    padding-top: 100px;
    padding-left: 100px;
}
.Game{
    padding: 100px;
    display: flex;
    flex-direction: row;
}
.board-row{
    display: table;
}
.status{
    font-size: 20px;
    color: gray;
    padding: 10px;
    padding-left: 0;
}
.moves{
    list-style-type: none;
    margin-top: 5px;
}
.moves button:hover{
    background: rgb(71, 71, 71);
    color: white;
}
.moves button{
    border: 1px solid rgb(136, 136, 136);
    background: white;
    -webkit-transition: .3s;
    transition: .3s;
    padding: 5px;
    margin-right: 5px;
}
.square{
    width: 100px;
    height: 100px;
    text-align: center;
    float: left;
    font-size: 60px;
    color: #555;
    font-weight: bold;
    background: white;
    border: 1px solid gray;
    margin-right: -1px;
    margin-top: -1px;
}
.square:focus{
    outline: none;
}
